.assessment-container {
  margin: 20px;
  border-radius: 15px;
  border: solid 2px $yellow;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-y: scroll;
  height: 400px;
  box-shadow: $box-shadow-black;

  .assessment-data {
    padding: 20px 30px 20px 30px;
    border-radius: 10px;
    border: solid 2px $yellow;
    box-shadow: $box-shadow-black;
    height: 100px;
    margin: 10px 20px 10px 20px;
  }

  .assessment-single {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .assessment-remove {
    border: 0;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    background: $yellow;
    font-size: x-large;
    color: $black;
  }
}